import YearDropdown from "components/Common/YearDropdown";
import StyledButton from "components/StyledButton";
import { useState } from "react";
import { useRecoilValue } from "recoil";
import { currentUserState } from "store/user";
import styled from "styled-components";

const GraduatePresenter = () => {
    const [graduateYear, setGraduateYear] = useState<string>("2021");

    const currentUser = useRecoilValue(currentUserState);
    const entranceYear: number = currentUser.data?.entranceYear
        ? parseInt(currentUser.data?.entranceYear)
        : 2017;

    return (
        <Wrapper>
            <StyledMain>
                <StyledSection>
                    <StyledH1>
                        {currentUser.data?.name}   
                        !
                    </StyledH1>
                    <StyledP>
                              
                        <br />
                               !
                        <br />
                        <i>
                                  
                               .
                        </i>
                    </StyledP>
                </StyledSection>

                <StyledSection>
                    <StyledH1> </StyledH1>
                    <YearDropdown
                        name=""
                        startYear={entranceYear + 1}
                        graduateYear={graduateYear}
                        setGraduateYear={setGraduateYear}
                    />
                </StyledSection>

                <StyledButton type="submit" label="" />
            </StyledMain>
        </Wrapper>
    );
};

export default GraduatePresenter;

const Wrapper = styled.div`
    width: 100vw;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const StyledMain = styled.main`
    width: 578px;
    padding: 10px 12px;
    background-color: white;

    display: flex;
    flex-direction: column;
`;

const StyledSection = styled.section`
    margin-bottom: 24px;
`;

const StyledH1 = styled.h1`
    font-size: 22px;
    color: ${({ theme }) => theme.colors.bold};
`;

const StyledP = styled.p`
    font-size: 12px;
    opacity: 0.7;
`;
