import { useState } from "react";
import styled from "styled-components";

import {
    ISignSettingChild,
    SettingChildDiv,
    NextBtn,
} from "pages/signinSetting/presenter/SignInSettingPresenter";

interface IAgreement extends ISignSettingChild {
    userName: string | undefined;
}

const Agreement = ({ onNextClick, userName }: IAgreement) => {
    const [isAgree, setIsAgree] = useState<boolean>(false);

    const onChange = () => {
        setIsAgree(!isAgree);
    };

    const checkValidation = () => {
        if (isAgree) onNextClick();
        else alert("   !");
    };

    const memberAgreement: string =
        "1.       ( '')   ,    , ,       . 2.    -                      . -                 e-mail   ,     .";

    const privacyAgreement: string =
        "  1511, 1711, 231, 241 1     ,      .          ,      .                    .";

    return (
        <SettingChildDiv>
            <AgreementWelcome>
                 <strong>{userName}</strong> !
            </AgreementWelcome>
            <AgreementHeading></AgreementHeading>
            <AgreementDiv>
                <p>{memberAgreement}</p>
            </AgreementDiv>
            <AgreementHeading>    </AgreementHeading>
            <AgreementDiv>
                <p>{privacyAgreement}</p>
            </AgreementDiv>

            <CheckDiv>
                <Check
                    id="agreement"
                    type="checkbox"
                    checked={isAgree}
                    onChange={onChange}
                />
                <CheckLabel htmlFor="agreement">  </CheckLabel>
            </CheckDiv>

            <NextBtn onClick={checkValidation}></NextBtn>
        </SettingChildDiv>
    );
};

export default Agreement;

const AgreementWelcome = styled.h2`
    color: ${({ theme }) => theme.colors.emph};
    margin-bottom: 12px;
    font-weight: 100;

    & strong {
        font-weight: 700;
    }
`;

const AgreementHeading = styled.h2`
    color: ${({ theme }) => theme.colors.emph};
    font-size: 1rem;
    margin-bottom: 6px;
`;

const AgreementDiv = styled.div`
    width: 100%;
    height: 60px;
    overflow: scroll;
    margin-bottom: 18px;
`;

const CheckDiv = styled.div`
    width: 100%;
    height: 50px;

    display: flex;
    justify-content: center;
    align-items: center;
`;

const Check = styled.input`
    all: unset;
    width: 1rem;
    height: 1rem;
    background-color: white;
    cursor: pointer;
    transition: all 0.3s;

    &:checked {
        border-radius: 50%;
        box-shadow: 0 0 0 3px ${({ theme }) => theme.colors.emph} inset;
        background-color: ${({ theme }) => theme.colors.bold};
    }
`;

const CheckLabel = styled.label`
    font-size: 1rem;
    font-weight: bold;
    color: ${({ theme }) => theme.colors.emph};
    margin-left: 10px;
    cursor: pointer;
    transition: color 0.3s;

    ${Check}:checked + & {
        color: ${({ theme }) => theme.colors.bold};
    }
`;
