function loadSuggestions(text: string): any {
    return new Promise((accept, reject) => {
        setTimeout(() => {
            const suggestions = [
                {
                    preview: "",
                    value: "# ",
                },
                {
                    preview: "",
                    value: "> ",
                },
                {
                    preview: "",
                    value: "** **",
                },
                {
                    preview: " ",
                    value: "[](url)",
                },
                {
                    preview: "",
                    value: "![ ](url)",
                },
            ].filter((i) =>
                i.preview.toLowerCase().includes(text.toLowerCase())
            );
            accept(suggestions);
        }, 250);
    });
}

export default loadSuggestions;
